const azerP=<p>Azr Zeynalov 1964-c il dekabrn 13-d Naxvan hrind ziyal ailsind anadan olmudur. 1969-cu ildn Bakda yaayan Azr 1972-ci ild 132 sayl orta mktb v 12 sayl musiqi mktbinin fortepiano sinfin getmidir. 1982-ci ild Azrbaycan Dvlt Mdniyyt v ncsnt Universitetinin dram rejissorluu bsin daxil olmu, arada 2 il hrbi xidmt getmi v 1989-cu ild dram rejissoru ixtisas il thsilini baa vurmudur. 1990-c ild musiqiy olan sevgisi onu Rid Behbudov adna Dvlt Mahn Teatrna gtirmi v bir mnni kimi o, ilk addmlarn bu teatrn shnsind atmdr. Brabr olaraq Rafiq Babayevin 'Cngi' ansamblnda da faliyyt gstrmi v hmin il 'Odlar Yurdu Bak-90' mahn msabiqsinin laureat seilmidir. 1992-ci ild 2-ci thsil almaq n Bak Musiqi Akademiyasnn vokal bsin daxil olmudur. 1994-c ild taliyada qbul imtahan verrk 'Aqostino Steffani' adna (Kastelfranko Veneto hri) taliya Dvlt Konservatoriyasnn vokal bsin qbul olmu, orada dahi italyan mnnisi Mario del Monakonun vaxtil tlbsi v asistenti olmu professor Alemanno Osvaldodan 2 il vokal drsi alm, 1996-c ild 'Opera ifas' ixtisas il oran bitirib Azrbaycana dnm v Bak Musiqi Akademiyasnda yarmq qalm thsilini d baa vurmudur. 1996-c ildn Azrbaycan Dvlt Akademik Opera v Balet Teatrnn solistidir. 'Liriko-dramatik tenor' ss tembrin malik olan Azr Zeynalov Avropann v Azrbaycann klassik operalarnda ba rollarn ifasdr. Azr Zeynalov tk opera ifas kimi yox, hm d Azrbaycan bstkarlarnn mahn v romanslarn, oda v oratoriyalarn da ifa edir. Buna Emin Sabitolunun 'kriyy', 'Gncdn glirm', Tofiq Quliyevin 'Bxtvr oldum', 'Azrbaycan' ('Bxtiyar' filmindki 'Rusiya'. Bu srin Azrbaycana hsr edilmsi ideyas v onun ilk ifas Azr Zeynalova mxsusdur v mahnnn ikinci bndi mnninin atas air Zeynal Vfa trfindn qlm alnb), Rauf Hacyevin 'Leyla' (szlri ncdn rus dilind olan bu mahnya ana dilind mtnin yazlmas A.Zeynalovun tbbsdr), Ramiz Mirilinin 'Vtn haray, Ruhngiz Qasmovann 'Sninlym, Azrbaycan', Ramiz Mustafayevin 'Mhmmd v Leyla', Vasif Adgzlovun 'Qm karvan', 'Tntnli kantata', 'anaqqala', 'Qaraba ikstsi', Mobil Babayevin 'lm-qara qarqa', Azr Dadaovun 'Ana yurdu' odas, Siyavu Kriminin 'mrmn istyi', 'Snsizlmim', Sevil liyevann 'ziz anam' v s. bu kimi srlri misal kmk olar. Onun repertuarnda xalq mahnlar da xsusi yer tutur. 'kriyy' adlanan ilk CD-albomu 1997-ci ild Trkiynin RAQS irkti trfindn iq z grmdr. z ifas il xarici dvltlrin byk konsert salonlarnda, o cmldn taliya, Avstriya, ngiltr, Fransa, sve, Almaniya, exiya, Hollandiya, in, Trkiy, KTR, Rusiya, Belarus, Moldova, Qaqauz, Ukrayna, Dastan, Grcstan, Tacikistan, Baqrdstan, Qrzstan,Qazaxstan, Tatarstan, Trkmnistan, zbkistan, Macarstan, Hindistan, Albaniya, Mrake, Misir, Sudiyy rbistan, Vyetnam, AB, Kanada v s. lklrd dflrl x etmidir. 2007-ci ild Azrbaycan zvvarlar il Hcc ziyartind olmudur. Azr Zeynalov bir ox mkafatlara layiq grlmdr. 1995-ci ild 'Avropa Trk-slam Kltr Cmiyyti' trfindn qzl medalla tltif olunmudur. Opera shnsind ifa etdiyi bir ox rollara gr mxtlif mkafatlar almdr. 1998-ci ild birbaa olaraq Azrbaycan Respublikasnn Xalq artisti, 2008-ci ild is Dastan Respublikasnn Xalq artisti fxri adna layiq grlmdr.  2016-2017-ci tdris ilindn etibarn pedaqoji faliyytini yalnz Azrbaycan Milli Konservatoriyasnda davam etdirrk burada Milli vokal kafedrasna rhbrlik edir. Azrbaycan Respublikas Prezidenti yannda Ali Attestasiya Komissiyas trfindn 2009-cu ild ona Vokal kafedras zr Dosent, 2013-c ild is Professor elmi ad verilmidir. 2008-ci ildn vokal ifalnn tdrisi n maqistr v bakalavr thsil pillsi zr bir ox fnnlrdn proqramlar trtib etmidir. Onun 2012-ci ild apdan xard 'Vokal ifal' kitab Ali musiqi mktblrind klassik opera-vokal ixtisas zr thsil alan tlblr n ana dilimizd ilk drslikdir. 2001-2003-c illrd, 2009, 2010, 2011, 2012, 2013-c il, 30 aprel 2014-c ild, 6 may 2015-ci ild[2], 6 may 2016-c ild, 1 may 2017-ci ild, 9 may 2018-ci ild, 10 may 2019-cu ild, 7 may 2020-ci ild, 7 may 2021-ci ild, 10 may 2022-ci ild[9] v 6 may 2023-c ild[10] Prezident Mkafatna layiq grlmdr.Azrbaycann dahi mtfkkiri Nsrddin Tusinin nslindndir. Aillidir. Drd vlad var.

air, publisist Zeynal Vfann oludur.</p>

